import { Component, OnInit, HostBinding, Input, HostListener, Output, EventEmitter } from '@angular/core';

@Component({
    selector: 'farris-rate',
    templateUrl: './rate.component.html',
    styleUrls: ['./rate.component.css']
})
export class RateComponent implements OnInit {

    @HostBinding('class.farris-star-rating') fsr = true;
    @Input() set value(value: any) {
        this.realScore = value;
    }
    // ææå¤§å°
    @Input() size = 'large';
    // å¯ç¨åé¢ææ¨¡å¼
    @Input() enableHalf = false;
    // å¯ç¨åæ¬¡ç¹å»åæ¸é¤
    @Input() enableClear = false;
    // åªè¯»
    @Input() disabled = false;
    // åå¶
    @Input() pointSystem = 5;
    // ææäº®è²
    @Input() lightColor: string;
    // æææè²ï¼åºè²ï¼
    @Input() darkColor: string;
    // å¾æ¡æ ·å¼
    @Input() iconClass = 'f-icon-star';

    // ç¦ç¨åä¸ªææçæå­æç¤º
    @Input() toolTipDisabled = true;
    // é»è®¤çæ»¡æåº¦ææ¡
    @Input() toolTipContents = ['å¾ä¸æ»¡æ', 'ä¸æ»¡æ', 'ä¸è¬', 'æ»¡æ', 'éå¸¸æ»¡æ'];
    // å¯ç¨è¯å
    @Input() enableScore = true;
    // å¯ç¨æ»¡æåº¦
    @Input() enableSatisfaction = false;
    @Output() valueChange = new EventEmitter();
    pointInfo: string;
    // ä¸é¢æå®½åº¦
    oneStarWidth: number;
    // äº®è²åºåèæå®½åº¦
    lightStarAreaWidth: number;
    // çé¢ä¸­äº®è²åºåçå®å®½åº¦
    realWidth = 0;
    // èæåæ°
    score: any = 0;
    // æç»åæ°
    realScore = 0;
    // æ¯ç
    rate = 1;
    // åæ¢çæ»¡æåº¦
    satisfaction: any;
    // æç»æ¾ç¤ºçæ»¡æåº¦
    realSatisfaction: any;
    // éç½®
    config = [];
    // ææä¹é´çé´è·,è®¾è®¡ç¨¿æ¯4
    distance = 4;
    constructor() { }

    ngOnInit() {
        let starId: any;
        switch (this.size) {
            case 'small':
                this.oneStarWidth = 14;
                break;
            case 'middle':
                this.oneStarWidth = 16;
                break;
            case 'large':
                this.oneStarWidth = 18;
                break;
        }
        this.config = [
            {
                id: 1
            },
            {
                id: 2
            },
            {
                id: 3
            },
            {
                id: 4
            },
            {
                id: 5
            }
        ];
        if (this.config.length) {
            this.rate = this.pointSystem / this.config.length;
        }
        if (this.realScore) {
            if (this.enableHalf) {
                starId = Math.ceil(this.realScore / this.rate);
            } else {
                starId = this.realScore;
            }
            this.realWidth = (this.oneStarWidth * this.realScore + Math.floor(this.realScore) * this.distance) / this.rate;
            this.satisfaction = this.toolTipContents[starId - 1]
        }
    }

    @HostListener('mouseover', ['$event'])
    onMouseOver(event: any) {
        if (!this.isTargetElement(event.target)) {
            return;
        }
        const id = parseInt(event.target.id);
        this.operateStar(event, id, false);
    }

    @HostListener('mousemove', ['$event'])
    onMouseMove(event: any) {
        if (!this.isTargetElement(event.target)) {
            return;
        }
        const id = parseInt(event.target.id);
        this.operateStar(event, id, false);
    }

    @HostListener('mouseout', ['$event'])
    onMouseOut(event: any) {
        if (!this.isTargetElement(event.target)) {
            return;
        }
        this.lightStarAreaWidth = 0;
        this.score = 0;
        this.satisfaction = null;
    }

    @HostListener('click', ['$event'])
    onClick(event: any) {
        if (!this.isTargetElement(event.target)) {
            return;
        }
        const id = parseInt(event.target.id);
        this.operateStar(event, id, true);
    }

    /**
     * æ¯å¦æ¯ç®æ åç´ 
     * @param element 
     */
    private isTargetElement(element: any) {
        if (element.className.indexOf('f-icon') !== -1) {
            return true;
        } else {
            return false;
        }
    }

    /**
     *  åæ¬¡ç¹å»åæ¶éä¸­
     */
    private clear() {
        if (this.lightStarAreaWidth === this.realWidth && this.enableClear && this.realWidth !== 0) {
            this.realWidth = 0;
            this.realScore = 0;
            this.realSatisfaction = null;
            this.valueChange.emit(this.realScore);
            return true;
        }
        return false;
    }
    /**
     * 
     * @param event äºä»¶
     * @param id å½åææçid
     * @param token æ¯å¦ç¹å»äºä»¶è§¦å
     */
    private operateStar(event: any, id: any, token: boolean) {
        // ç¦ç¨
        if (this.disabled) {
            return;
        }
        // æ»¡æåº¦ææ¡
        if (this.enableSatisfaction) {
            this.satisfaction = this.toolTipContents[id - 1];
        }
        // å¦ææ»å¨çè·ç¦»å°äºä¸é¢ææä¸åçè·ç¦»ï¼æèè¶è¿ï¼
        if (event.offsetX < this.oneStarWidth / 2 && this.enableHalf === true) {
            this.lightStarAreaWidth = (this.oneStarWidth * id) - this.oneStarWidth / 2 + this.distance * (id - 1);
            this.score = (this.rate * id) - this.rate / 2;
            if (token === true) {
                if (this.clear()) {
                    return;
                }
                this.realScore = this.score;
                this.valueChange.emit(this.realScore);
                this.realWidth = this.lightStarAreaWidth;
                this.realSatisfaction = this.satisfaction;
            }
        } else {
            this.lightStarAreaWidth = this.oneStarWidth * id + this.distance * (id - 1);
            this.score = this.rate * id;
            if (token === true) {
                if (this.clear()) {
                    return;
                }
                this.realScore = this.score;
                this.valueChange.emit(this.realScore);
                this.realWidth = this.lightStarAreaWidth;
                this.realSatisfaction = this.satisfaction;
            }
        }
    }
}
